.b-cta__about{
  .nav-list li{ 
    width:100%;
    float:left;

    a{
    width:100%;
    float:left;
    @include btn_icon( $black, darken($black,10), $white, "\e6bd",  "true");
    //@include add-noise;
    //background-color:$black;
    @include adjust-font-size-to($font-size-small-2);
    @include rhythm(0, $ms-4n, $ms-4n, 0, $font-size-small-2);
    @include trailing-border(ms(-7), $ms-2n, $font-size-small-2, solid);
    text-transform:none;
    @include breakpoint($bp-mobile1-1){
      font-family:$BdCn;
      font-weight:800;
    } 
   }//end a

   &.dropdown{
    

     .dropdown-toggle{
       @include btn_icon( $black, darken($black,15), $white, "\e643",  "true");
       @include standard-transition(font-size);
       @include adjust-font-size-to($font-size-small-2);
       @include rhythm(0, $ms-4n, $ms-4n, 0, $font-size-small-2);
       @include trailing-border(ms(-7), $ms-2n, $font-size-small-2, solid);

      text-transform:none;

      }
      &.open .dropdown-toggle{
        @include btn_icon( $quadrary, darken($quadrary,15), $black, "\e643",  "true");
        @include adjust-font-size-to($font-size-small-2+ms(-8));
        @include rhythm(0, $ms-4n, $ms-4n, 0, $font-size-small-2);
        text-transform:none;
        &:hover:after{
            color:$white;
            border:0;
        }
        &:after{ 
          content: "\e644"; 
          color:$black;
          border:darken($quadrary, 8) ms(-11) solid;

        }//end :after
      }//end &.open .dropdown-toggle
      .dropdown-menu{
          margin-top:-1px;
          padding:0 ms(-2);              
          @include add-noise;
          background-color: set-alpha(darken($black,10), 0.92);
          width:100%;
          @include trailing-border(ms(-10), $ms-2n, $font-size-small-2, solid);
          border-bottom-color:shade($black,10);
          border-top-left-radius:0;
          border-top-right-radius:0;
          box-shadow: 0 ms(-3) ms(-5) 0 shade($black,20) inset;
          top:111%;


          li a{
            @include btn_icon( $coolblack, darken($coolblack, 15), $white, "\e6bd",  "true");
            @include trailing-border(ms(-7), $ms-2n, $font-size-small-2, solid);
            @include adjust-font-size-to($font-size-small-2);
            white-space:normal;
          }

          .dropdown-header{
            @include adjust-font-size-to($font-size-small);
            text-transform:uppercase;
            color:$WarmGrey2;
            float:left;
            @extend .font-Roman;
            white-space:normal;
            padding-left:0;
          }
      }//end .dropdown-menu

    }//end dropdown

  }//end .nav-list li
}//end b-cta__about