.s-footer__wvu--wrapper{
	width:100%;
	float:left;
	background:set-alpha(darken($black, 5), 0.55);
	margin-top:-3px;
	

  #s-footer__wvu{
  	background: url(../images/wvu-footer-bg.jpg) center top $SecBlue;
  	border-color:darken($tertiary,10);
  	border-top-color:$tertiary;
  	box-shadow: 0 3px 1px darken($primary, 10), 0 6px 9px darken($black,10);
  	border-style: solid;
  	border-width: ms(-11) ms(-11) 0 ms(-11);
  	padding: 1rem 1.25% 0 1.25%;

	.container{
	  margin: 0 auto;
	  font-size:16px;
	  //background:url(data:image/jpeg;base64,) #005daa 54% top;
	  

	  p{
	  	@include adjust-font-size-to($font-size-small);
	  	color:$white;
	  }
	  .wvu-links a, .wvu-credits a{
	  	color:$quadrary;
	  	text-decoration: underline;
	  	@include adjust-font-size-to($font-size-small);
	  	&:hover{
	  		color:tint($quadrary, 20);
	  	}
	  }
	  .wvu-links li::before, .wvu-credits li::before{
	  	color:darken($tertiary, 12);
	  } 
	  .wvu-links{
	  	padding-bottom:0 ;
	  }
	  

	  
	}// end .container
  }//end #wvu-footer
}//end .wvu-footer-wrapper

